.sponsor-header {
  background-color: #252a34;
  color: #fff;
  padding: 80px 0;
}

.section-header {
  text-align: center;
  margin-bottom: 40px;
}

.title1 {
  font-size: 36px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.sponsor-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: 900px;
    padding: 20px;
  }
  .left-image {
    width: 100px;
    height: auto;
    left: 10%;
    margin: 20px;
    border-radius: 50%;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  }
  .middle-image {
    width: 200px;
    height: auto;
    right: 10%;
    margin: 20px;
    border-radius: 20%;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  }
  .right-image {
    width: 200px;
    height: auto;
    left: 10%;
    margin: 20px;
    border-radius: 20%;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  }
  @media only screen and (max-width: 768px) {
    .sponsor-header1 {
      padding-top: 50px;
      padding-bottom: 50px;
      text-align: center;
    }
    
    .section-header1 {
      margin-bottom: 20px;
    }
    
    .title1 {
      font-size: 28px;
      text-align: center;
      text-transform: uppercase;
      color: #fff;
    }
    
    .sponsor-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    
    .sponsor-image {
      max-width: 90%;
      height: auto;
    }
    
    .left-image {
      order: 1;
    }
    
    .middle-image {
      order: 2;
    }
    
    .right-image {
      order: 3;
    }
  }
  